// src/templates/post.js
import React from "react"
import "../styles/post.css"
import Layout from "../components/layout";
import { Badge,Container } from "react-bootstrap"

export default function Post({ pageContext }) {
    const { title, createdAt, image } = pageContext.post;
    const body = pageContext.post.body.childMarkdownRemark.html;
    const tags = pageContext.post.metadata.tags;

    return (
        <Layout class="border mx-3">
            <div className="post-header">
                <h1>{title}</h1>
                <p className="post-date">{createdAt}</p>
                <Container>
                    {tags && tags.map(({ contentful_id, name }) =>
                        <Badge bg="secondary" className="me-3">{name}</Badge>
                    )}
                </Container>
            </div>

            <div className="post-image-div">
                <img src={image.file.url} className="post-image" alt="post-cover"></img>
            </div>
            <div dangerouslySetInnerHTML={{ __html: body }} className="post-body" />
        </Layout>
    )
}